import { j as json } from "../../../../chunks/index.js";
import { P as Product } from "../../../../chunks/product.js";
async function GET() {
  try {
    const products = await Product.findAll({
      order: [["name", "ASC"]]
    });
    return json(products);
  } catch (error) {
    console.error("get products error", error);
    return { status: 500 };
  }
}
export {
  GET
};
